.footer,
.left,
.middle,
.right {
	display: flex;
	align-items: center;
}

.left,
.right,
.copyright {
	color: #242b2d;
	opacity: 0.4;
}

.footer {
	display: flex;
	align-items: center;
	height: 150px;
	background-color: #fff;
	justify-content: space-around;
}

.middle {
	flex-direction: column;
}

.copyright {
	position: relative;
	top: 25px;
}

.link {
	cursor: pointer;
	margin-right: 10px;
	transition: all 0.2s;
}

.link:hover {
	color: #0aaee4;
}
